@import url(https://fonts.googleapis.com/css?family=Quicksand:400,500,700&subset=latin-ext);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200;1,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Quicksand:400,500,700&subset=latin-ext);
@import url(https://fonts.googleapis.com/css2?family=Shippori+Mincho:wght@400;700&display=swap);
.profile {
  width: 300px;
  height: 450px;
  display: block;

  border-radius: 5px;
  border-width: 3px;
  border-color: #271829;
  border-style: outset;

  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
  margin: 50px;
}
.description {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.profile_name {
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  font-size: 24px;
  color: #c144ff;
}
.profile_tag,
.profile_location {
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  font-size: 15px;
  color: #540c79;
}
.profile_name,
.profile_tag,
.profile_location {
  margin-top: 0;
  margin-bottom: 10px;
}
.profile_avatar {
  width: 200px;
  height: 250px;
  border-radius: 50%;
  display: block;

  padding: 0;
  margin: 0;
}
.profile_stats {
  list-style: none;
  display: flex;
  flex-direction: row;
  padding: 0;
}
.profile_stats_item {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.profile_stats_item:not(:last-child) {
  padding-right: 30px;
}
.label {
  margin-bottom: 10px;

  font-weight: 500;
  margin-top: 7px;
}
.quantity {
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  font-size: 27px;
}

.statis_section {
  width: 400px;
  text-align: center;
  background: linear-gradient(45deg, #d5135a, #f05924);
}
.statis_title {
  margin-top: 0;
  margin-bottom: 15px;
  font-family: 'Poppins', sans-serif;
  font-weight: 700;
  color: rgb(255, 255, 255);
}
.statis_list {
  display: flex;
  flex-direction: row;
  align-items: center;

  margin: 0;
  padding: 0;

  list-style: none;
}
.statis_wrapper {
  width: 80px;

  display: flex;
  flex-direction: column;
  align-items: center;

  border-width: 2px;
  border-style: solid;
  border-color: rgb(255, 255, 255);
}
.statis_label {
  padding-bottom: 10px;
  font-weight: 200;
}
.statis_percent {
  font-weight: 700;
}
.statis_label,
.statis_percent {
  font-family: 'Poppins', sans-serif;
  color: rgb(255, 255, 255);
}

.FriendList_friend_list__2tfjg {
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  display: block;

  list-style: none;
  margin: 50px;
}
.FriendList_friend_item__1-lWt {
  background: linear-gradient(45deg, #1da1f2, #0e71c8);
  box-shadow: 0px 4px 30px rgba(19, 127, 212, 0.4);

  width: 300px;
  padding: 10px;
  border-radius: 5px;

  display: flex;
  flex-direction: row;
}
.FriendList_isOnline__kh2HG,
.FriendList_isOffline__BmEUu {
  display: block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin: 0 20px 0 20px;
}
.FriendList_isOnline__kh2HG {
  background-color: green;
}
.FriendList_isOffline__BmEUu {
  background-color: red;
}

.FriendList_friend_item__1-lWt:not(:last-child) {
  margin-bottom: 15px;
}
.FriendList_friend_item__1-lWt:hover {
  box-shadow: 0px 7px 30px rgba(19, 127, 212, 0.75);
}
.FriendList_friend_name__m8Osv {
  margin: 0;
  margin-left: 15px;
  color: rgba(255, 255, 255);
}

.trans_history {
  background-color: #eee7db;
  text-align: center;
  border: 2px solid #a40808;
}
.trans_header,
.trans_item {
  border: 1px solid #aaaaaa;
  padding: 3px 2px;
}
.trans_item:nth-child(even) {
  background: #f5c8bf;
}

.trans_head {
  background-color: #a40808;
}
.trans_header {
  width: 150px;

  font-family: 'Shippori Mincho', serif;
  font-weight: 700;
  font-size: 20px;
}

